import { Component } from '@angular/core';
declare var jQuery: any;

@Component({
  selector: '[dialogs]',
  template: require('./dialogs.template.html'),
  styles: [ require('./dialogs.style.scss') ]
})
export class Dialogs {

  ngOnInit() {
    jQuery('[data-toggle="popover"]').popover();
  }

}



